import {Grid, Paper} from "@mui/material";
import Typography from "@mui/material/Typography";
import CheckIcon from "@mui/icons-material/Check";
import ClearIcon from "@mui/icons-material/Clear";
import React from "react";

// @ts-ignore
export default function AttemptFactory({attempt}) {
    const defaultStyle = {
        display: "flex",
        flexWrap: "wrap",
        "& > :not(style)": {
            m: 1,
            width: 260,
            height: 40,
        },
    }
    const emptyAttempt = (attempt: any) => {
        return (<Grid item
                      sx={defaultStyle}
        >
            <Paper
                style={{backgroundColor: "gray"}}>
                <Grid container direction={"row"} justifyContent={"space-between"}>
                    <Grid item p={1}>
                        <Typography>
                            {attempt.answer}
                        </Typography>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>)
    }
    const failedAttempt = (attempt:any) =>{

        return (<Grid item
              sx={defaultStyle}
        >
            <Paper
                style={{backgroundColor: "red"} }>
                <Grid container direction={"row"} justifyContent={"space-between"}>
                    <Grid item p={1}>
                        <Typography>
                            {attempt.answer}
                        </Typography>
                    </Grid>
                    <Grid item p={1}>
                        <ClearIcon/>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>)
    }
    const successfulAttempt = (attempt:any) =>{
        return(<Grid item
              sx={defaultStyle}
        >
            <Paper
                style={{backgroundColor: "green"} }>
                <Grid container direction={"row"} justifyContent={"space-between"}>
                    <Grid item p={1}>
                        <Typography>
                            {attempt.answer}
                        </Typography>
                    </Grid>
                    <Grid item p={1}>
                        <CheckIcon/>
                    </Grid>
                </Grid>
            </Paper>
        </Grid>)
    }
    if(attempt.answer === ''){
        return emptyAttempt(attempt)
    }
    switch (attempt.isCorrect) {
        case false:
            return failedAttempt(attempt)
        case true:
            return successfulAttempt(attempt)
        default:
            return <div></div>
    }
}